
#app{
    height: 50px;
    width: 100%;
    overflow: hidden;
}
#app nav{
    height: 60px;
    padding: 0 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: middle;
    -ms-flex-align: middle;
    align-items: middle;
    overflow: scroll;
}
#app p{
    text-align: center;
    font-size: 16px;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    padding: 10px;
    margin: 5px;
}
#app p.active{
    color: #ffff00;
    background-color: #000000;
}
